.Intro-Container {
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
  height: 100vh;
  align-items: center;
  width: 100vw;
  padding-top: 20px; 
}

.Intro-Name {
  display: flex;
  flex-direction: column; 
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.bio-Intro {
  text-align: center;
  text-decoration: wavy;
  max-width: 600px; 
  color: white;
}

.top-NavigationIcon {
  display: flex;
  flex-direction: column;  
  top:25px;
  align-items: center;  
  justify-content: center; 
  width: 100%; 
  position: absolute;
}

.bottom-NavigationIcon {
  display: flex;
  flex-direction: column;  
  align-items: center;  
  justify-content: center; 
  width: 100%; 
  padding-bottom: 20px;  
}

.bottom-NavigationIcon ul {
  margin: 0; 
  padding: 0; 
  list-style-type: none; 
  text-align: center; 
  color: white;
}

.ProfilePitchure{
  width: auto;
  height: 40vh;
  margin: 20px auto 0 auto; 
  border-radius: 12px;
  box-shadow: 0 0 15px 5px rgba(0, 162, 255, 0.6);
}

.Title {
  font-size: 30px;
  font-weight: bold;
  font-style: italic;
  color: white;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.extra-site {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../Image/123.png'); 
  transition: height 6s ease; 

}

.extra-site.top {
  top: 0;
  transform-origin: top;
  animation: openTop 6s forwards;


}

.extra-site.bottom {
  bottom: 0;
  width: 100%;
  transform-origin: bottom;
  animation: openBottom 6s forwards;
}

.content {
  text-align: center;
  opacity: 0;
  animation: fadeIn 2s forwards 2s;
  z-index: 2;
  position: relative;
  color: rgb(255, 0, 0);
}

@keyframes openTop {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}

@keyframes openBottom {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

p{
  font-size:x-large;
  text-align: center;
  align-items: center;
  justify-content: center;
}



.work-Section {
  position: absolute;
  bottom: -100%;
  width: 100%;
  min-height: 100vh;  
  background-color: black;
  color: white;
  transition: bottom 2s ease;
}





.bottomButton {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(0%);
  z-index: 1000;
  cursor: pointer;
}

.work-Content {
  position: relative;
  height: 100%;
}


.NavigationImage {
  height: 50px;
  width: 50px;
  cursor: pointer;
  border-radius: 60px;
  z-index: 3;
  transition: transform 0.3s ease;
}

.NavigationImage:hover {
  transform: scale(1.1);
}

.h1-WorkSection{
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: xx-large;
  position: relative;
  top: 100px;
}
.work-Section.fadeOut {
  opacity: 0;
  transition: opacity 1s ease, bottom 2s ease;
}


.Pbio{
  font-size: 1.3rem;
  line-height: 1.6; 
  text-align: center;
  color: #ffffff;
  max-width: 600px;
  margin: 20px auto; 
  padding: 0 20px; 
  font-weight: 300; 
  letter-spacing: 0.5px; 
  
}

@media (max-width: 768px) {
  .Intro-Container {
    padding-top: 10px;
    height: auto;
    min-height: 100vh;
  }

  .Title {
    font-size: 22px;
    padding: 0 10px;
  }

  .ProfilePitchure {
    height: 30vh;
    margin-top: 10px;
  }

  .Pbio {
    font-size: 1rem;
    line-height: 1.5;
    padding: 0 15px;
    margin: 15px auto;
    max-width: 90%;
  }

  .NavigationImage {
    height: 40px;
    width: 40px;
  }

  .h1-WorkSection {
    font-size: large;
    top: 60px;
  }
}

@media (max-width: 480px) {
  .Title {
    font-size: 20px;
  }

  .ProfilePitchure {
    height: 25vh;
  }

  .Pbio {
    font-size: 0.95rem;
    line-height: 1.4;
  }

  .NavigationImage {
    height: 35px;
    width: 35px;
  }

  .h1-WorkSection {
    font-size: medium;
    top: 40px;
  }
}
